import React from 'react'
import { Box, DataTable, Meter, Heading, Paragraph, Text } from 'grommet'
import { Fade, Bounce, Zoom, Rotate, Roll } from 'react-reveal';

const Row = (props) => {
    const { icon, index, could, need } = props
    return (
        <Box direction="row" gap="small" justify="around" flex>
            <Box alignSelf="center">{icon}</Box>
            <Heading level="2">{index}</Heading>
            <Heading level="2">{could}</Heading>
            <Heading level="2">{need}</Heading>
        </Box>
    )
}

class SalaryResult extends React.Component {
    render() {
        const { data } = this.props
        const rows = data.map((object, index) => <Row key={index} {...object} />)

        return (
            <React.Fragment>

                <Fade bottom cascade>

                    <Row index="Índice" could="Podías" need="Necesitas" />
                    {rows}

                </Fade>

            </React.Fragment >
        )
    }
}

export default SalaryResult